import ajax from '../libs/ajax'

export const getInitData = params =>
  ajax.request({
    url: '/lolita-im/i18n',
    params,
    method: 'get'
  })

export const getGallery = params =>
  ajax.request({
    url: '/lolita-im/gallery',
    params,
    method: 'get'
  })

export const getFriendLink = params =>
  ajax.request({
    url: '/lolita-im/links',
    params,
    method: 'get'
  })

export const getSysInfo = params =>
  ajax.request({
    url: '/common/version',
    params,
    method: 'get'
  })

export const getBlogList = (id,params) =>
  ajax.request({
    url: '/blog/list/' + id,
    params,
    method: 'get'
  })

export const getBlogById = (id,params) =>
  ajax.request({
    url: '/blog/post/' + id,
    params,
    method: 'get'
  })