import Vue from 'vue'
import Vuex from 'vuex'
import { getInitData } from '@/api'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    configData:{}
  },
  mutations: {
    SET_CONFIG_DATA (state, configData) {
      state.configData = configData
    }
  },
  actions: {
    LOAD_CONFIG_DATA ( {commit},lang ){
      getInitData({ lang:lang }).then((res)=>{
        commit('SET_CONFIG_DATA', res.results)
      })
    }
  }
})
